*{
    background-color:#2A2D40;
    text-align: center;
    color: white;
}
h1{
    font-size: 10px;
}
h2{
    font-size: 40px;
}
.card{
    padding: 0px;
    margin: 40px;
    width: 24%;
    list-style: none;
    display: inline-block;
}
.card__main,.card__main_2,.card__main_3,.card__main_4,.card__main_5,
.card__main_6{
    padding: 0px;
    margin: 0px;
    width: 100%;
    list-style: none;
    display: inline-block;
}
.card__inner,.card__inner_2,.card__inner_3,.card__inner_4,.card__inner_5
,.card__inner_6{
    text-align: center;
}
.card__name > span,.card__name_2 > span,.card__name_3 > span,.card__name_4 > span,.card__name_5 > span
,.card__name_6 > span{
    font-size: 11px;
    font-weight: 500;
    color: floralwhite;
}
.card__name,.card__name_2,.card__name_3,.card__name_4,.card__name_5
,.card__name_6{
    font-size: 22px;
    font-weight: 800;
    color: floralwhite;
}

.card__total,.card__total_2,.card__total_3,.card__total_4,.card__total_5
,.card__total_6{
    font-size: 40px;
    font-weight: 700;
    color: floralwhite;
}

.card__main, .card__inner, .card__name > span, .card__name, .card__total
{
    background-color:dodgerblue;
}

.card__main_2, .card__inner_2, .card__name_2 > span, .card__name_2, .card__total_2
{
    background-color:green;
}
.card__main_3, .card__inner_3, .card__name_3 > span, .card__name_3, .card__total_3
{
    background-color:saddlebrown;
}
.card__main_4, .card__inner_4, .card__name_4 > span, .card__name_4, .card__total_4
{
    background-color:chocolate
}
.card__main_5, .card__inner_5, .card__name_5 > span, .card__name_5, .card__total_5
{
    background-color:dodgerblue;
}
.card__main_6, .card__inner_6, .card__name_6 > span, .card__name_6, .card__total_6
{
    background-color:darkgoldenrod
}


